/** Media Queries **/
html,
body {
  height: 100%;
  margin: 0;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
}

p {
  color: #000000;
  font-size: 2rem;
}

/* Estilos clase layout*/
.layout {
  display: contents;
}

.contenedor {
  width: 95%;
  max-width: 100vw;
  margin: 0 auto;
}
.contenedor h1::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #F25A66;
  margin: 1rem auto;
  margin-bottom: 7rem;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
  font-weight: 900;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
  font-weight: 700;
}

h4 {
  font-size: 2.6rem;
}

@media (min-width: 375px) {
  .seccion {
    margin-top: 6rem;
    margin-bottom: 3rem;
  }
}
@media (min-width: 768px) {
  .seccion {
    margin-top: 9rem;
    margin-bottom: 4rem;
  }
}
.seccion.nosotros, .seccion.anuncios, .seccion.blog, .seccion.contacto, .seccion.login, .seccion.propiedad, .seccion.vendedor, .seccion.panel-admin {
  margin-top: 16rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.boton-salmon-block {
  background-color: #F25A66;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 2rem;
  text-align: center;
  border-radius: 1rem;
  display: block;
  border: none;
}
.boton-salmon-block:hover {
  background-color: #f04250;
  cursor: pointer;
  transition: ease-in-out 0.5s;
}

.boton-salmon {
  background-color: #F25A66;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 2rem;
  text-align: center;
  border-radius: 1rem;
  display: inline-block;
  border: none;
}
.boton-salmon:hover {
  background-color: #f04250;
  cursor: pointer;
  transition: ease-in-out 0.5s;
}

.btn-salmon {
  border: 2px solid #F25A66;
  color: #f04250;
  font-size: 2rem;
  font-weight: 700;
  padding: 1rem 2rem;
  border-radius: 1rem;
  text-align: center;
}
.btn-salmon:hover {
  background-color: #F25A66;
  color: #FFFFFF;
  transition: ease-in-out 0.5s;
}

.boton-rojo-block {
  background-color: rgb(248, 66, 66);
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 2rem;
  text-align: center;
  border-radius: 1rem;
  display: inline-block;
  border: none;
}
.boton-rojo-block:hover {
  background-color: #f72929;
  cursor: pointer;
  transition: ease-in-out 0.5s;
}

.boton-verde-block {
  background-color: #71B100;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 2rem;
  text-align: center;
  border-radius: 1rem;
  display: inline-block;
  border: none;
}
.boton-verde-block:hover {
  background-color: #619800;
  cursor: pointer;
  transition: ease-in-out 0.5s;
}

.darkMode {
  background-color: #1a1a1a;
  transition: all 0.5s;
  /* Clase men√∫ hamburguesa */
}
.darkMode p {
  color: #EAEAEA;
}
.darkMode a,
.darkMode h1,
.darkMode h2,
.darkMode h3,
.darkMode h4 {
  color: #FFFFFF;
}
.darkMode td {
  color: #FFFFFF;
}
.darkMode .anuncio {
  background-color: #333333;
  border: 1px solid #333333;
  transition: all 0.5s;
}
.darkMode .anuncio .icono {
  filter: invert(100%);
}
.darkMode .resumen-propiedad .precio {
  color: #F25A66;
}
.darkMode .resumen-propiedad p {
  color: #EAEAEA;
}
.darkMode .resumen-propiedad .icono {
  filter: invert(100%);
}
.darkMode .mostrar {
  background-color: #1a1a1a;
  transition: all 0.5s;
}
.darkMode .mostrar p {
  color: #EAEAEA;
}
.darkMode .mostrar a,
.darkMode .mostrar h1,
.darkMode .mostrar h2,
.darkMode .mostrar h3,
.darkMode .mostrar h4 {
  color: #FFFFFF;
}
.darkMode .mostrar .close-menu {
  filter: invert(100%);
}
@media (min-width: 375px) {
  .darkMode .navegacion {
    background-color: #1a1a1a;
    transition: all 0.5s;
  }
  .darkMode .navegacion p {
    color: #EAEAEA;
  }
  .darkMode .navegacion a,
  .darkMode .navegacion h1,
  .darkMode .navegacion h2,
  .darkMode .navegacion h3,
  .darkMode .navegacion h4 {
    color: #FFFFFF;
  }
  .darkMode .navegacion .close-menu {
    filter: invert(100%);
  }
}
@media (min-width: 1024px) {
  .darkMode .navegacion {
    background-color: transparent;
  }
}
.darkMode .navegacion-dark-mode {
  border: 1px solid #FFFFFF;
}
@media (min-width: 1024px) {
  .darkMode .navegacion-dark-mode {
    border: none;
  }
}
.darkMode .dark-mode-boton {
  filter: invert(100%);
}
.darkMode .texto-nosotros blockquote {
  color: #FFFFFF;
}
.darkMode .testimonial {
  background-color: #333333;
}
.darkMode .testimonial blockquote {
  color: #FFFFFF;
}
.darkMode .testimonial p {
  color: #F25A66;
}
.darkMode .formulario label,
.darkMode .formulario legend,
.darkMode .formulario p {
  color: #EAEAEA;
}
.darkMode .formulario input:not([type=submit]),
.darkMode .formulario select,
.darkMode .formulario textarea {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}

/** UI **/
.no-webp .inicio {
  background-image: url("../img/header-2.jpg");
}

.webp .inicio {
  background-image: url("../img/header-2.webp");
}

.header.inicio {
  background-position: center center;
  background-size: cover;
  position: relative;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 60rem;
  }
}
.header.inicio .contenido-header {
  padding: 10rem 2rem 3.5rem 2rem;
}
@media (min-width: 375px) {
  .header.inicio .contenido-header {
    min-height: 60rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding-bottom: 6rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 60rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding-bottom: 9rem;
  }
}
.header.inicio .barra {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 90;
  transition: 0.7s;
  background-color: transparent;
}
.header.inicio .barra.abajo {
  background-color: #333333;
  padding: 1rem 2rem;
}
.header .barra {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 90;
  transition: 0.7s;
  background-color: #333333;
}

@media (min-width: 768px) {
  .contenido-header {
    padding: 0;
  }
}
.contenido-header .texto-header h2 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 1.2;
  max-width: 60rem;
}
.contenido-header .texto-header h4 {
  font-weight: 400;
  color: #FFFFFF;
  line-height: 1.2;
  max-width: 60rem;
  margin-top: 0;
  margin-bottom: 3.5rem;
}

.footer {
  padding: 2.5rem;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
  gap: 2.5rem;
}
.footer .navegacion a {
  color: #FFFFFF;
}
.footer .navegacion a:hover {
  color: #F25A66;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.barra {
  display: flex;
  justify-content: space-between;
  padding: 2rem;
  transition: 0.7s;
}
.barra.abajo {
  background-color: #333333;
  padding: 1rem 2rem;
}
.barra .logo {
  width: 15rem;
}
@media (min-width: 768px) {
  .barra {
    align-items: flex-end;
    align-items: center;
  }
}

.navegacion {
  /* Navegaci√≥n telefonos y tablets */
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  width: 60%;
  background-color: #FFFFFF;
  padding: 2rem;
  z-index: 200;
  border-radius: 2.4rem 0 0 0;
  transform: translateX(100%);
  transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out;
}
.navegacion .container-closeBtn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}
.navegacion .container-closeBtn p {
  margin: 0;
}
@media (min-width: 1024px) {
  .navegacion {
    position: static;
    z-index: auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: transparent;
    width: auto;
    opacity: 1;
    visibility: visible;
    transform: none;
  }
}
.navegacion .enlace {
  color: #333333;
  text-decoration: none;
  font-size: 2rem;
  display: block;
  text-align: left;
  transition: color 0.3s ease;
}
.navegacion .enlace:hover {
  color: #F25A66;
}
@media (min-width: 1024px) {
  .navegacion .enlace {
    margin: 0 1rem 0 1rem;
    font-size: 2rem;
    color: #FFFFFF;
  }
  .navegacion .enlace:last-of-type {
    margin-right: 0;
  }
}

.navegacion-menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
@media (min-width: 1024px) {
  .navegacion-menu {
    flex-direction: row;
  }
}

.navegacion-enlaces {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .navegacion-enlaces {
    flex-direction: row;
    align-items: center;
  }
}

.navegacion-dark-mode {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  border-radius: 4rem;
  border: 1px solid;
  padding: 1rem 2rem;
}
.navegacion-dark-mode:hover {
  cursor: pointer;
}
@media (min-width: 1024px) {
  .navegacion-dark-mode {
    border: none;
    padding: 1rem 2rem;
  }
}
.navegacion-dark-mode p {
  color: #333333;
  font-weight: bold;
}

.dark-mode-boton {
  width: 3rem;
}
@media (min-width: 1024px) {
  .dark-mode-boton {
    margin: 0 1rem 0 1rem;
    width: 2rem;
    filter: invert(100%);
  }
}

#dark-mode-texto {
  margin: 0;
}
@media (min-width: 1024px) {
  #dark-mode-texto {
    display: none;
  }
}

/** Navegaci√≥n mobile **/
.menu-mobile,
.close-menu {
  display: flex;
