}
.contenedor-table .table-header {
  display: flex;
  align-items: center;
  gap: 2rem;
  padding: 2rem;
  position: sticky;
  top: 0;
  z-index: 2;
}
.contenedor-table .table-header a {
  display: flex;
  align-items: center;
  gap: 1rem;
}
